import Vue from 'vue'
import Router from 'vue-router'
// import ZhongYiFu from '@/pages/ZhongYiFu'
import ZhengFu from '@/pages/ZhengFu/index.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/zhengfu'
    },
    // {
    //   path: '/zhongyifu',
    //   name: 'ZhongYiFu',
    //   component: ZhongYiFu
    // },
    {
      path: '/zhengfu',
      name: 'ZhengFu',
      component: ZhengFu
    }
    // {
    //   path: '/hello',
    //   name: 'Hello',
    //   component: Hello
    // }
  ]
})



